import React, { Component } from "react";
import searchIcon from "./svgs/Search.svg";
import backSlashIcon from "./svgs/backSlash.svg";

class SearchBar extends Component {
  render() {
    return (
      <div className="h-10 sm:w-72 w-full border-light  items-center border-2 rounded-lg flex flex-row justify-between px-3 ">
        <img className="h-4 w-auto" src={searchIcon} alt="" />
        <div className="flex justify-between w-full">
          <input
            className="w-full mr-4 ml-4 font-sans text-xs text-opacity-50 focus:outline-none "
            placeholder="Search"
          />
          <img
            className="relative h-4 w-auto invisible sm:visible"
            src={backSlashIcon}
            alt=""
          />
        </div>
      </div>
    );
  }
}

export default SearchBar;
