import React, { Component } from "react";
import demo from "./svgs/th.jpg";
import dp from "./svgs/dp.png";

function StoryCard(props) {
  return (
    <div className="flex flex-col mx-4 my-4 bg-gray-100 justify-evenly border-1a border-gray-300 rounded-3xl w-max max-w-screen-sm h-auto max-h-96 px-4 py-4">
      <h1 className="text-lg text-black font-medium my-3">{props.story.title}</h1>
      <div className="grid grid-cols-6 gap-2 my-3">
        <div className="col-span-3 flex flex-col justify-between m-1">
          <img className="rounded-2xl flex-1 h-72 object-fill" src={props.story.image} alt=""></img>
          <div className="px-4 py-2 flex flex-row justify-between">
            <div className="">
              <div className="text-sm font-medium text-black"></div>
              <div className="text-xs font-medium text-gray-400">
                {props.story.date.time}
              </div>
            </div>
            {/* <img
              className="h-8 w-8 self-center rounded-full object-cover"
              src={dp}
              alt=""
            ></img> */}
          </div>
        </div>
        <div className="col-span-3">
          <p className="text-sm font-normal max-h-60 text-gray-600 overflow-hidden break-words">
            {props.story.content}
          </p>
        </div>
      </div>
    </div>
  );
}

export default StoryCard;
