import React, { Component } from "react";
import ContentBar from "./ContentBar";
import StoryCard from "./StoryCard";
import ArticleCard from "./ArticleCard";
import HorizontalScroll from "react-scroll-horizontal";
import firebase from "./firebase.js";
import { getFirestore, collection, getDocs } from 'firebase/firestore/lite';

function HomeBody() {
  const [stories, setStories] = React.useState([]);

  React.useEffect(() => {
    const fetchData = async () => {
      const db = getFirestore(firebase);
      const storiesCol = collection(db, 'stories');
      const data = await getDocs(storiesCol);
      setStories(data.docs.map(story => story.data()));
    };
    fetchData();
  }, []);

  let list = [];

  for (let i = 0; i < stories.length; i++) {
    list[i] = <StoryCard story={stories[i]} />;
  }

  return (
    <div className="flex flex-col overflow-auto h-full w-screen bg-white pt-3 ">
      <ContentBar title="Stories" subTitle="Short news" />
      <div className="w-full h-96">
        <HorizontalScroll>{list}</HorizontalScroll>
      </div>
      <ContentBar title="Articles" subTitle="Your crypto library" />
      <ArticleCard />
    </div>
  );
}

export default HomeBody;
