import React, { Component } from "react";
import logo from "./svgs/logo.svg";
import playStore from "./svgs/play.svg";
import market from "./svgs/Chart.svg";

class Topbar extends Component {
  render() {
    return (
      <div className="relative bg-white inset-x-0 top-0 content-center ">
        <div className="max-w-full mx-auto px-4 sm:pt-4">
          <div className="flex justify-between items-center  py-6 ">
            <div className="flex justify-start ">
              <a href="#">
                <img className="h-6 w-auto" src={logo} alt="" />
              </a>
            </div>

            <div className="flex space-x-4 items-center ">
              <div
                className="h-8 w-96 border-light  items-center border-2 rounded-lg flex flex-row px-3 sm:visible invisible hover:shadow-lg hover:border-0"
                onClick={() => console.log("x")}
              >
                <img className="h-3 w-auto" src={market} alt="" />
                <marquee className="w-auto ml-4 font-sans text-xs text-opacity-50  ">
                  Bitcoin - $30k (▲ 4.67% ) Dogecoin ▼ 2.01% Bitcoin ▲ 4.67%
                  Doge
                </marquee>
              </div>

              <a href="#">
                <img className="h-4 w-auto " src={playStore} alt="" />
              </a>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Topbar;
